:root {
  --up-color: #1261c4;
  --down-color: #c84a31;
  --new-color: #16c784;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

[class*="icon-"] {
  margin-right: 4px;
}
[class*="icon-"]:before {
  display: inline-block;
  font-family: CMC V2;
  font-style: normal;
  font-weight: 400;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.icon-Caret-up,
.icon-Caret-up {
  opacity: 1;
}
.icon-Caret-up:before {
  font-size: 16px;
  -webkit-transform: scale(0.7);
  transform: scale(0.7);
  vertical-align: middle;
  content: "▲";
}
.icon-Caret-down:before {
  font-size: 16px;
  -webkit-transform: scale(0.7);
  transform: scale(0.7);
  vertical-align: middle;
  content: "▼";
}

.layout .content-comp .content-area,
.theme-light .layout .content-comp .content-area {
  padding-top: 0;
}

.ct-option {
  position: relative;
}
.ct-option .count {
  position: absolute;
  color: #7a7c85;
  font-size: 13px;
  white-space: nowrap;
  top: 0;
  right: 0;
}
.ct-item:hover .count {
  opacity: 0;
  display: none;
}